import React, { Component } from 'react';
import {
    Scene,
    PerspectiveCamera,
    WebGLRenderer,
    SphereGeometry,
    Geometry,
    Math,
    PointsMaterial,
    Points,
    Vector3,
    MeshPhongMaterial,
    Mesh,
    TextureLoader,
    AmbientLight,
} from 'three';
import OrbitControls from 'three-orbitcontrols';
import mapTexture from '../assets/icon2.png';

export class Globe extends Component {
    componentDidMount = () => {
        this.scene = new Scene();
        this.camera = new PerspectiveCamera( 75, window.innerWidth/window.innerHeight, 0.1, 1000 );
        this.renderer = new WebGLRenderer();
        this.renderer.setSize( window.innerWidth, window.innerHeight );
        this.mount.appendChild( this.renderer.domElement );
        this.addGlobe();
        this.addLighting();
        this.camera.position.z = 4;
        this.controls = new OrbitControls( this.camera, this.renderer.domElement );
        this.controls.update();
        this.addStars();
        this.animate();
    }
    addStars = () => {
        let starsGeometry = new Geometry();
        for ( let i = 0; i < 10000; i ++ ) {
            let star = new Vector3();
            star.x = Math.randFloatSpread( 2000 );
            star.y = Math.randFloatSpread( 2000 );
            star.z = Math.randFloatSpread( 2000 );
            starsGeometry.vertices.push( star );
        }
        let starsMaterial = new PointsMaterial( { color: 0x777777 } );
        let starField = new Points( starsGeometry, starsMaterial );
        this.scene.add( starField );
    }
    addGlobe = () => {
        let geometry = new SphereGeometry(1, 40, 40);
        let loader = new TextureLoader();
        let texture = loader.load(mapTexture);
        let material = new MeshPhongMaterial({
            color: 0xffffff,
            specular: 0xffffff,
            shininess: 100,
            map: texture,
            specularMap: texture,
            normalMap: texture
        });
        this.cube = new Mesh( geometry, material );
        this.scene.add( this.cube );
    }
    addLighting = () => {
        this.light = new AmbientLight( 0xffffff, 1.2);
        this.scene.add( this.light );
    }
    animate = () => {
        requestAnimationFrame( this.animate );
        this.cube.rotation.y += 0.001;
        this.controls.update();
        this.renderer.render( this.scene, this.camera );
    }
    render() {
        return (
            <div ref={ref => (this.mount = ref)} />
        )
    }
}

export default Globe;